
import { Element, Action } from '@ali/4ever-cangjie';
import type { TagData } from '@ali/4ever-plugin-tag';

export const TAG_REMOVE = Symbol('tag-remove');
export const TAG_SET_DATA = Symbol('tag-set-data');
export const TAG_INSERT = Symbol('tag-insert');


export interface RemoveTagPayload {
  node: Element;
}
export interface SetTagDataPayload {
  node: Element;
  mergeData: Record<string, any>;
}
export type InsertTagPayload = Partial<TagData>;

export function removeTag(
  node: Element,
): Action<RemoveTagPayload> {
  return {
    type: TAG_REMOVE,
    payload: {
      node,
    },
  };
}
export function setTagData(
  node: Element,
  mergeData: Partial<TagData>,
): Action<SetTagDataPayload> {
  return {
    type: TAG_SET_DATA,
    payload: {
      node,
      mergeData,
    },
  };
}
export function insertTag(data: InsertTagPayload) {
  return {
    type: TAG_INSERT,
    payload: {
      ...data,
    },
  };
}
